<template>
<div>
        <Avatar :src="imgSrc" size='large'/>
        <Upload :action="actionPath" :data='placeholderCanteen' :on-success='uploadAfter' :show-upload-list='false'>
        <Button icon="ios-cloud-upload-outline">修改头像</Button>
    </Upload>
        <br>
         <Form :model="formLeft" label-position="left" :label-width="100">
        <FormItem label="食堂标签">
            <Input v-model="formLeft.canteenLabel" :placeholder=" placeholderCanteen.canteenLabel"></Input>
        </FormItem>
        <FormItem label="食堂描述">
            <Input v-model="formLeft.canteenDes" :placeholder=" placeholderCanteen.canteenDes"></Input>
        </FormItem>
           <FormItem label="食堂名字">
            <Input v-model="formLeft.canteenName" :placeholder=" placeholderCanteen.canteenName"></Input>
        </FormItem>
        <Button @click="updateCanteenInfo" type="primary">修改食堂信息</Button>
    </Form>  
     <br>      
    
</div>
</template>
<script>
import {updateCanteen,getOneCanteen} from '@/api/api.js'
export default {
    data(){
        return{
            imgSrc:'',
            canteenId:this.$route.query.canteenId,
            canteenLabel:'',
            canteenDes:'',
            actionPath:'',
            placeholderCanteen:{
                canteenLabel:'',
                canteenDes:'',
                canteenName:'',
                canteenId:this.$route.query.canteenId,
            },
            formLeft: {
                    canteenLabel: '',
                    canteenDes: '',
                    canteenName: '',
                    canteenId:this.$route.query.canteenId,
                },
                
        }
        
    },
    created(){
        let data={canteenId:this.canteenId}
        getOneCanteen(data).then(res=>{
            let obj=res.data.data
            this.imgSrc=obj.canteenImage
            this.placeholderCanteen.canteenLabel=obj.canteenLabel
            this.placeholderCanteen.canteenDes=obj.canteenDes          
            this.placeholderCanteen.canteenName=obj.canteenName
        },err=>{

        })
    },
    mounted(){
        this.actionPath='http://www.canteen.com/api/adminServiceZuul/Canteen/uploadCanteenImg'     
    },
    updated(){

    },
    methods:{
            uploadAfter (res,file) {
                if(res.data!=null){
                    this.imgSrc=res.data
                   this.$Message.success('修改食堂头像成功')
                }
            },
            updateCanteenInfo(){
                updateCanteen(this.formLeft).then(res=>{
                    if(res.status==200){
                         this.$Message.success('修改食堂信息成功')
                         //this.$router.push({name:'sellerMain'})                        
                    }
                    else{
                        this.$Message.error('修改食堂信息失败')
                    }             
                },err=>{
                    this.$Message.error('网络出错')
                })
            }

    }

}
</script>
